.login-cover{
    background-color: white;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items:center ;
    justify-content: center;
    overflow: hidden;
}
.image-page-sign{
   width: 100%;
    
}


.overal-login{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Roboto", sans-serif;
    font-size: 13px;
    margin-top: 50px;
  
}

.input-login{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    gap: 10px;
  padding-left: 50px;
 padding-right: 50px;
}

.login-btn{
    margin-top: 10px;
    background-color:#ea1179;
    border:none;
    color: white;
    border-radius: 5px;
    padding: 10px;
    width: 100%;
    font-weight: 600;
    margin-left: 50px;
    margin-right: 50px;
    cursor: pointer; 
}

.btn-login-style{
    display: flex;
    align-items: center;
    justify-content: center;
}

.Link-signup-login{
background-color: white;
    color: #ea1179;
    border: 1px solid #ea1179;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border-radius: 5px;
    width: 100%;
    font-weight: 600;
    cursor:pointer;
    margin: 10px 0px;
    margin-bottom: 30px;

}


@media (max-width: 768px) {

    .overal-login{
        font-size: 13px;
        margin-top: 0px;

       

    }

    .login-cover{
        background-color: white;
        width: 100%;
      
        
      
    }
   
}